@media (max-width: 430px) {

    .bg-mg {
        width: 100%;
        box-sizing: content-box;
    }

    .manu-mg {
        width: 100%;
        /* padding: 0px 13px; */
    }

    .manu-mg a {
        width: 25%;
    }

    .menu-btn {

        height: 70px;
        width: 95%;
    }

    .img-sz-mg {

        width: 36px !important;

    }

    .btn {
        width: 5rem;
        font-weight: bold;
        border-radius: 5px;
        padding: 2px 0px;
        font-size: 14px;


    }

    .cash-f-mg {
        padding: 5px;
        gap: 3px;
    }


    .progersh-mg-cnt {
        width: 130%;
        height: 18px;
    }

    .show-cash {
        padding: 5px;

    }





    .spin-mg {
        /* display: none; */
        height: fit-content;
        margin: 5px 0px;

    }


    .bg-img-mg {

        width: 350px;
        height: 350px;


    }


    .black-ring {

        width: 270px;
        height: 270px;

    }

    .blackCircle {
        width: 300px;
        height: 300px;

    }

    .click-point {

        width: 55px;
        height: 55px;
    }

    .reward-finder {

        width: 270px;
        height: 270px;
        top: 14%;

    }
    


    .ref-link-copy {
        margin-top: 5px;
        height: 25px !important;

    }

    .big-link {

        padding: 5x 0px;

    }

    .invite-text {

        font-size: 15px;

    }

    .cashf-img {
        height: 25px;

    }

    .tap-to-copy {
        width: 10%;
        padding: 10px;

        font-size: 16px;

    }

/* ****************** invte page css ****** */

